import { FC } from "react";
import { message } from "antd";

import IcBsc from "../../assets/icons/ic-bsc.svg";
import IcEth from "../../assets/icons/ic-eth.svg";
import IcMatic from "../../assets/icons/ic-matic.png";
import IcKcs from "../../assets/icons/ic-kcs.png";
import ChainIds from "../../configs/chainIds";
import SwitchNetworkItem from "./SwitchNetworkItem";
import styles from './SwitchNetworkForm.module.scss';
import { useCallback } from "react";
import { switchNetwork } from "../../packages/web3/helper";

type Props = {
  onClose?(): void;
};

type TChain = {
  chainId: ChainIds;
  icon: string;
  name: string;
  currency: {
    name: string;
    symbol: string;
    decimals: number;
  };
  rpcUrls: string[];
  blockExplorerUrls: string[];
};

const chains: TChain[] = [
  {
    name: "Ethereum Mainnet",
    chainId: ChainIds.ETH_MAINNET,
    icon: IcEth,
    currency: {
      name: "ETH",
      symbol: "ETH",
      decimals: 18,
    },
    rpcUrls: ["https://mainnet.infura.io/v3/20e078e98de64af88b26c6b1bb47f822"],
    blockExplorerUrls: ["https://etherscan.io"]
  },
  {
    name: "Binance Smart Chain Mainnet",
    chainId: ChainIds.BSC_MAINNET,
    icon: IcBsc,
    currency: {
      name: "BNB",
      symbol: "BNB",
      decimals: 18,
    },
    rpcUrls: [
      "https://bsc-dataseed1.ninicoin.io",
      "https://bsc-dataseed1.defibit.io",
      "https://bsc-dataseed.binance.org",
    ],
    blockExplorerUrls: ["https://bscscan.com"]
  },
  {
    name: "Matic(Polygon) Mainnet",
    chainId: ChainIds.MATIC_MAINNET,
    icon: IcMatic,
    currency: {
      name: "MATIC",
      symbol: "MATIC",
      decimals: 18,
    },
    rpcUrls: ["https://rpc-mainnet.maticvigil.com"],
    blockExplorerUrls: ["https://polygonscan.com/"]
  },
  {
    name: "KuCoin Community Chain Mainnet",
    chainId: ChainIds.KCC_MAINNET,
    icon: IcKcs,
    currency: {
      name: "KuCoin Token",
      symbol: "KCS",
      decimals: 18,
    },
    rpcUrls: ["https://rpc-mainnet.kcc.network", "wss://rpc-ws-mainnet.kcc.network"],
    blockExplorerUrls: ["https://explorer.kcc.io/en"]
  },
];

const testnets: TChain[] = [
  {
    name: "Binance Smart Chain Testnet",
    chainId: ChainIds.BSC_TESTNET,
    icon: IcBsc,
    currency: {
      name: "BNB",
      symbol: "BNB",
      decimals: 18,
    },
    rpcUrls: [
      "https://data-seed-prebsc-1-s1.binance.org:8545",
      "https://data-seed-prebsc-2-s1.binance.org:8545",
      "https://data-seed-prebsc-1-s2.binance.org:8545",
    ],
    blockExplorerUrls: ["https://testnet.bscscan.com"]
  },
  {
    name: "Matic Testnet Mumbai",
    chainId: ChainIds.MATIC_TESTNET,
    icon: IcMatic,
    currency: {
      name: "MATIC",
      symbol: "MATIC",
      decimals: 18,
    },
    rpcUrls: ["https://rpc-mumbai.matic.today"],
    blockExplorerUrls: ["https://explorer-mumbai.maticvigil.com/"]
  },
];

const SwitchNetworkForm: FC<Props> = ({ onClose }) => {
  const handleClickItem = useCallback((item: TChain) => () => {
    (async () => {
      try {
        onClose?.();
        const switched = await switchNetwork({
          chainId: item.chainId,
          name: item.name,
          currency: {
            name: item.currency.name,
            symbol: item.currency.symbol,
            decimals: item.currency.decimals,
          },
          rpcUrls: item.rpcUrls,
          blockExplorerUrls: item.blockExplorerUrls,
        });
        if (!switched) alert(`To switch to ${item.name} please do it manually from your wallet menu`);
      } catch (e) {
        message.error('Cannot switch network');
      }
    })();
  }, [onClose]);
  return (
    <>
      <div className={styles.chains}>
        {chains.map((chain) => (
          <SwitchNetworkItem
            key={chain.chainId}
            name={chain.name}
            icon={chain.icon}
            onClick={handleClickItem(chain)}
          />
        ))}
      </div>
      <div className={styles.divider} />
      <div className={styles.chains}>
        {testnets.map((chain) => (
          <SwitchNetworkItem
            key={chain.chainId}
            name={chain.name}
            icon={chain.icon}
            onClick={handleClickItem(chain)}
          />
        ))}
      </div>
    </>
  );
};

export default SwitchNetworkForm;
